"use client";

import Link from "next/link";
import { usePathname } from "next/navigation";
import { cn } from "@/lib/utils";
import {
    LayoutDashboard,
    ShoppingCart,
    Wallet,
    Users,
    Settings,
    LogOut,
    Package,
    History,
    Gift
} from "lucide-react";

const menuItems = [
    { name: "Dashboard", href: "/dashboard", icon: LayoutDashboard },
    { name: "Buy Data", href: "/dashboard/orders/new", icon: ShoppingCart },
    { name: "My Orders", href: "/dashboard/orders", icon: Package },
    { name: "Wallet", href: "/dashboard/wallet", icon: Wallet },
    { name: "Referrals", href: "/dashboard/referrals", icon: Users },
    { name: "Commissions", href: "/dashboard/commissions", icon: Gift },
    { name: "Transactions", href: "/dashboard/transactions", icon: History },
];

export function Sidebar() {
    const pathname = usePathname();

    return (
        <aside className="w-64 h-screen bg-card border-r flex flex-col fixed left-0 top-0 z-50 transition-all duration-300">
            <div className="p-6">
                <Link href="/dashboard" className="flex items-center gap-2">
                    <div className="w-8 h-8 bg-primary rounded-lg flex items-center justify-center text-primary-foreground font-bold text-xl">
                        M
                    </div>
                    <span className="text-xl font-bold tracking-tight">Megaai</span>
                </Link>
            </div>

            <nav className="flex-1 px-4 py-4 space-y-1 overflow-y-auto">
                {menuItems.map((item) => {
                    const isActive = pathname === item.href;
                    return (
                        <Link
                            key={item.href}
                            href={item.href}
                            className={cn(
                                "sneat-sidebar-item",
                                isActive ? "active" : "text-muted-foreground hover:bg-accent hover:text-accent-foreground"
                            )}
                        >
                            <item.icon className="w-5 h-5" />
                            <span>{item.name}</span>
                        </Link>
                    );
                })}
            </nav>

            <div className="p-4 border-t">
                <button
                    onClick={() => {
                        localStorage.removeItem('token');
                        localStorage.removeItem('user');
                        window.location.href = '/auth/login';
                    }}
                    className="sneat-sidebar-item text-red-500 hover:bg-red-50 w-full"
                >
                    <LogOut className="w-5 h-5" />
                    <span>Logout</span>
                </button>
            </div>
        </aside>
    );
}
