"use client";

import { Bell, Search, User as UserIcon } from "lucide-react";
import { useEffect, useState } from "react";

export function Header() {
    const [user, setUser] = useState<{ name: string, email: string } | null>(null);

    useEffect(() => {
        const storedUser = localStorage.getItem('user');
        if (storedUser) {
            setUser(JSON.parse(storedUser));
        }
    }, []);

    return (
        <header className="h-16 bg-white/80 backdrop-blur-md sticky top-4 mx-4 z-40 rounded-lg shadow-sm border flex items-center justify-between px-6 transition-all duration-300">
            <div className="flex items-center gap-4 flex-1">
                <Search className="w-5 h-5 text-muted-foreground" />
                <input
                    type="text"
                    placeholder="Search (Ctrl+/)"
                    className="bg-transparent border-none outline-none text-sm w-full max-w-xs"
                />
            </div>

            <div className="flex items-center gap-4">
                <button className="relative p-2 text-muted-foreground hover:bg-accent rounded-full">
                    <Bell className="w-5 h-5" />
                    <span className="absolute top-1.5 right-1.5 w-2 h-2 bg-red-500 rounded-full border-2 border-white"></span>
                </button>

                <div className="flex items-center gap-3 pl-4 border-l">
                    <div className="text-right hidden sm:block">
                        <p className="text-sm font-semibold leading-none">{user?.name || "User"}</p>
                        <p className="text-xs text-muted-foreground mt-1 capitalize">Member</p>
                    </div>
                    <div className="w-10 h-10 bg-primary/10 rounded-full flex items-center justify-center text-primary">
                        <UserIcon className="w-6 h-6" />
                    </div>
                </div>
            </div>
        </header>
    );
}
